#page {
	background-color: $body-background;
}

.sticky-post {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.updated:not(.published) {
	display: none;
}

.shadow-border {
	@include shadow-border;
}

.grid-borders {
	& > .cell {
		@include shadow-border;
	}
}

#content {
	min-height: 250px;
	padding-bottom: 1em;
}

.archive,
.blog {
	article {
		padding-bottom: 1rem;
		box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);

		&:hover {
			-webkit-transform: translate3D(0, -1px, 0);
			transform: translate3D(0, -1px, 0);
			box-shadow: 0 0 25px rgba(0, 0, 0, 0.5);
			transition: 0.5s ease all;
		}

		&.has-post-thumbnail {
			padding-top: 0;

			.featured-image {
				a {
					position: relative;
					display: block;
				}
			}
		}

		.post-inner {
			height: 100%;
		}

		.featured-image {
			padding: 0;

			figure {
				margin: auto;

				i,
				img {
					width: 100%;
					height: 225px;
					object-fit: cover;
					position: relative;
					z-index: 1;
				}

				i {
					&:before {
						@include absolute-center;
						font-size: 140px;
					}
				}
			}
		}

		.entry-title {
			margin-top: 0.5rem;

			a {
				word-break: break-word;
			}
		}

		&.sticky {
			width: calc(100% - 1.875rem) !important;

			.entry-title {
				a:before {
					content: '\f08d';
					font-family: 'FontAwesome';
					margin-right: 10px;
				}
			}
		}
	}
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

.single-post {
	.featured-image {
		img {
			margin: auto;
			display: block;
			max-height: 400px;
			max-width: 600px;
			object-fit: contain;
			width: auto;
		}
	}
}

.entry-meta {
	& > span:nth-child(n+2) {
		&:before {
			content: ' | ';
		}
	}
}

.entry-footer {
	span {
		display: block;
		margin-bottom: 1rem;
	}
}

.after-excerpt {
	width: 100%;
	display: flex;
	justify-content: flex-end;
	align-items: flex-end;
}



